.settings-route {
  background: #323232;

  form {
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    gap: 0.8rem 0;
    padding: 2.6rem 2rem;

    h3 {
      font-family: 'Noto Sans Bold';
      text-transform: uppercase;
      color: white;
      font-size: 11px;
      margin-top: 1.7rem;

      &:first-of-type {
        margin-top: 0;
      }
    }
  }

  .dropdown {
    margin-bottom: 1rem;

    h3 {
      padding-bottom: 0.2rem;
    }

    .temp-div {
      width: 330px;
      height: 32px;
      border-radius: 3px;
      background: hsl(0, 0%, 8%);

      &:hover {
        background: hsl(0, 0%, 5%);
      }

      /*  */
      color: white;
      display: flex;
      padding: 0 0.6rem;
      place-items: center;
    }
  }
}

.checkbox {
  height: 17px;

  label {
    position: relative;
    height: 17px;
    display: flex;
    place-items: center;
    place-content: center;
    flex-direction: row;
    width: max-content;

    &:hover {
      .check-fake {
        border: 1px solid #585858;
        background: #ffffff11;
      }
    }

    &:hover {
      input:checked ~ .check-fake {
        border: 1px solid #0DD166;
        background: #0DD166;
      }
    }

    span {
      position: relative;
      top: 0px;
      font-family: 'Noto Sans';
      font-size: 13px;
      color: white;
      height: 100%;
    }
  }

  .check-box {
    position: relative;
    width: 17px;
    height: 17px;

    border: 1px solid transparent;
    margin-right: 4px;

    &:focus-within {
      border: 1px solid white;
    }

    .check-fake {
      top: 0px;
      left: 0px;
      position: absolute;
      width: 15px;
      height: 15px;
      border: 1px solid #626262;
      display: flex;
      place-content: center;
      place-items: center;
      
      .check {
        display: none;
      }
    }

    input {
      opacity: 0;
      
      &:checked + .check-fake {
        border-color: #008542;
        background: #008542;

        .check {
          display: block;
        }
      }
    }
  }
}